@import url(https://fonts.googleapis.com/css2?family=Albert+Sans:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap);
footer, #call, #testimonials {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

footer, #call, #testimonials, #services, #hero {
  padding-top: 30px;
  padding-bottom: 50px;
}

body {
  margin: 0px;
  padding: 0px;
  border: 0px;
  font-family: "Albert Sans", sans-serif;
  text-decoration: none;
  color: #28395e;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3em;
}

#start {
  width: auto;
  min-height: 100vh;
  background-image: linear-gradient(to right, #059669, #043F6C);
}

#hero {
  display: flex;
  justify-content: center;
  color: #ddd;
  height: 100%;
  margin-top: 105px;
}
#hero h1 {
  font-size: 5em;
}
#hero h3 {
  font-size: 2em;
  margin-bottom: 70px;
}

#menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  min-height: 40px;
  margin-right: 30px;
}
#menu a {
  transition: ease 0.2s;
}
#menu a:hover {
  font-weight: bold;
  transition: ease 0.2s;
}

.menu-item {
  margin: 20px;
  text-decoration: none;
  color: #28395e;
}

#logo {
  width: 20%;
  height: auto;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  position: fixed;
  top: 0;
  width: 100%;
}

.container {
  width: 80%;
}

.hero-flex {
  display: flex;
}

.hero-container {
  width: 70%;
}

.responsive-image {
  width: 40%;
  height: auto;
}

#services {
  display: flex;
  justify-content: center;
  background-color: #D9F7E9;
}

.services-flex {
  display: flex;
  flex-direction: column;
}

#testimonials {
  justify-content: center;
  background-color: #7bdaac;
}
#testimonials h1 {
  color: white;
}

.testimonial {
  border: 2px;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 33%;
  background-color: white;
  margin: 20px;
  padding: 20px;
}

.testimonial-people {
  display: flex;
  flex-direction: row;
  align-content: space-around;
  padding: 20px;
}
.services-image {
  width: 50%;
}

.services-text-before {
  display: flex;
  margin: 25px;
}
.services-text-before div {
  margin-right: 5%;
}
.services-text-before img {
  border-radius: 10px;
}

.services-text-after {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
}

.section-title {
  text-align: center;
  margin-bottom: 60px;
}

#call {
  color: #ddd;
  background-color: #059669;
  text-align: center;
}
#call h3 {
  margin-bottom: 60px;
}

footer {
  background-image: linear-gradient(to bottom, #059669, #043F6C);
  color: #ddd;
}
footer .column-title {
  font-weight: bold;
}

.flex-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.rounded-button {
  color: #28395e;
  background-color: white;
  margin: 50px;
  padding: 30px;
  border-radius: 50px;
  font-weight: bold;
  transition: ease 0.3s;
}
.rounded-button:hover {
  background-color: #064E3B;
  color: white;
  border: 2px white;
  transition: ease 0.3s;
}

.footer-sections {
  display: flex;
  flex-direction: column;
}
.footer-sections a {
  color: white;
  margin-top: 20px;
}

.testimonial-people section, .services-image, .rounded-button {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.building {
  border: 10px solid white;
}